.mappedin-js-inspector {
	.list-items {
		max-height: 600px;
		overflow-y: scroll;
		scroll-behavior: smooth;
	}
	.children:focus {
		border: 1px solid blue;
	}
	#interactionPanel {
		position: absolute;
		right: 0;
	}
	#mainPanel {
		position: absolute;
		left: 0px;
	}
	.title {
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
}
